import GoogleLogin from  "@stack-pulse/next-google-login"

import saveToGoogleSheets from './func/saveToGoogleSheets'

function Login() {

    function onLogin(googleResponse){
        console.log(googleResponse);
        saveToGoogleSheets(googleResponse.profileObj.email);
    }
  return (
    <div className="GoogleLogin">
        <GoogleLogin
        clientId="796862051841-uf2qae34ci7rhqihf9smh6omhn1jjvic.apps.googleusercontent.com"
        buttonText="Login"
        onSuccess={onLogin}
        onFailure={console.log}
        cookiePolicy={'single_host_origin'}
        />
        
    </div>
  );
}

export default Login;
